"use client";
import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Home, DollarSign, Briefcase, Megaphone, TrendingUp, Users, Bell, Settings } from 'lucide-react';
import { motion } from 'framer-motion';
import './DashboardLauncher.css';

const sidebarLinks = [
    { path: '/dashboard', icon: <Home size={20} />, label: 'Overview' },
    { path: '/dashboard/finance', icon: <DollarSign size={20} />, label: 'Finance' },
    { path: '/dashboard/business', icon: <Briefcase size={20} />, label: 'Business' },
    { path: '/dashboard/development', icon: <Megaphone size={20} />, label: 'Development' },
    { path: '/dashboard/sales', icon: <TrendingUp size={20} />, label: 'Sales' },
    { path: '/dashboard/community', icon: <Users size={20} />, label: 'Community' },
];

const DashboardLauncher: React.FC = () => {
    const pathname = usePathname();

    return (
        <div className="os-launcher glass-panel animate-slide-up">
            <div className="launcher-nav">
                {sidebarLinks.map(link => {
                    const isActive = pathname === link.path;
                    return (
                        <Link
                            key={link.path}
                            href={link.path}
                            className={`launcher-item ${isActive ? 'active' : ''}`}
                        >
                            <div className="icon-wrapper">
                                {link.icon}
                            </div>
                            <span className="launcher-label">{link.label}</span>
                            {isActive && (
                                <motion.div
                                    layoutId="launcher-active"
                                    className="active-indicator"
                                />
                            )}
                        </Link>
                    )
                })}
            </div>
            <div className="launcher-divider" />
            <div className="launcher-actions">
                <button className="launcher-item" title="Notifications">
                    <Bell size={20} />
                </button>
                <button className="launcher-item" title="Settings">
                    <Settings size={20} />
                </button>
            </div>
        </div>
    );
};

export default DashboardLauncher;
